<template>
  <v-app>
    <v-row align="center" class="list-group px-3 mx-auto">
      <v-spacer></v-spacer>
      <!--  -->
      <v-row elevation="12" class="flex">
        <v-col cols="12" sm="8">
          <v-text-field v-model="search" clearable prepend-icon="mdi-magnify" label="" class="input-search" v-on:keyup.enter="page = 1; retrievePosts();"></v-text-field>
          <template >

          </template>
        </v-col>
        <v-col cols="12" sm="2">
          <v-btn
              class="mx-2"
              fab
              dark
              color="cyan"
              @click="page = 1; retrievePosts();"
          >
            <v-icon dark>
              mdi-magnify
            </v-icon>
          </v-btn>
        </v-col>
      </v-row>

      <!--    -->
      <v-col cols="12" sm="12">
        <v-card class="mx-auto" tile elevation="18">
          <template v-slot:expanded-item="{ headers, item }">
            <td :colspan="headers.length">
              More info about {{ item.title }}
            </td>
          </template>

          <!-- (, , ()) -->
          <v-card-title class="list">
            <v-row>
              <v-col cols="12" sm="4" class="ml-1">
                <h2></h2>
              </v-col>
              <!-- , -->
              <v-col cols="12" sm="2" class="ml-0 mr-0">
                <PostDialogFrom @addPost="addPost"></PostDialogFrom>
              </v-col>
              <!-- , -->
              <v-col cols="12" sm="2" class="ml-5">
                <v-btn tile dark color="green" @click="refreshList()">
                  <v-icon left>mdi-cached</v-icon>
                  
                </v-btn>
              </v-col>
              <!-- , -->
              <v-col cols="12" sm="2" class="ml-5">
                <v-btn class="mx-auto" tile dark color="error" @click="removeAllPosts">
                  <v-icon left>mdi-delete</v-icon>
                  
                </v-btn>
              </v-col>
            </v-row>
          </v-card-title>

          <!--  -->
          <v-row class="mx-auto">
            <v-col cols="4" sm="3">
              <v-select
                  v-model="pageSize"
                  :items="pageSizes"
                  label=""
                  @change="handlePageSizeChange"
              ></v-select>
            </v-col>

            <!-- () -->
            <v-col cols="12" sm="9">
              <v-pagination
                  v-model="page"
                  :length="totalPages"
                  circle
                  total-visible="7"
                  next-icon="mdi-menu-right"
                  prev-icon="mdi-menu-left"
                  @input="handlePageChange"
              ></v-pagination>
            </v-col>
          </v-row>
          <v-data-table
              :headers="headers"
              :items="posts"
              disable-pagination
              :hide-default-footer="true"
              :loading="loading"
              loading-text="..."
          >
            <template v-slot:[`item.author`]="{ item }">
              <v-chip pill>
                <v-avatar left>
                  <v-img src="http://img.aruoxi.top/image/favcionx32.ico"></v-img>
                </v-avatar>
                {{ item.author }}
              </v-chip>
            </template>
            <template v-slot:[`item.actions`]="{ item }">
<!--              <v-icon small class="mr-2" @click="editpost(item.id)">-->
<!--                mdi-pencil-->
<!--              </v-icon>-->
              <Post :index="item.index" :currentPost="item.post" :currentPost_copy="item.post" @cancelEdit="cancelEdit" @editPost="editPost"></Post>
              <v-icon small color="error" @click="deletePost(item.id)">
                mdi-delete
              </v-icon>
            </template>
          </v-data-table>
        </v-card>
      </v-col>
    </v-row>
  </v-app>
</template>

<script>
import PostDataService from "../services/PostDataService";
import PostDialogFrom from "@/components/PostDialogFrom";
import Post from "@/components/Post";

export default {
  name: "posts-list",
  components: { Post, PostDialogFrom },
  data() {
    return {
      alert: true,
      success_msg: false,
      warn_msg: false,
      msg: 'this is a alert message',
      timeout: 1100,
      posts: [],
      currentPost: null,
      currentIndex: -1,
      search: "",
      loading: true,
      headers: [
        { text: "", align: "start", sortable: false, value: "title" },
        { text: "", value: "content", sortable: false },
        { text: "", value: "author", sortable: false },
        { text: "/", value: "actions", sortable: false },
      ],
      page: 1,
      totalPages: 0,
      pageSize: 6,
      pageSizes: [3, 6, 9, "all"],
    };
  },
  methods: {
    // 
    getRequestParams(search, page, pageSize) {
      let params = {};

      if (search) {
        params["title"] = search;
      }

      if (page) {
        params["page"] = page - 1;
      }

      if (pageSize) {
        params["size"] = pageSize;
      }

      if (search){
        params["title"] = search;
        params["author"] = search;
      }

      return params;
    },
    handlePageChange(value) {
      this.page = value;
      this.retrievePosts();
    },

    handlePageSizeChange(size) {
      this.pageSize = size;
      this.page = 1;
      this.retrievePosts();
    },

    getDisplayPost(post) {
      return {
        id: post.id,
        title: post.title.substr(0, 20) + "...",
        content: post.content.substr(0, 20) + "...",
        author: post.author,
        post: post,
      };
    },

    // 
    addPost(addPost) {
      console.log(addPost.msg);
      if (addPost.success){
        this.$send(addPost.msg,'success');
      }
      else this.$send(addPost.msg,'error');
      this.refreshList();
    },

    // 
    editPost(editPost) {
      console.log(editPost.msg);
      // alert(editPost.id);
      if (editPost.success){
        this.$send(editPost.msg,'success');
      }
      else  this.$send(editPost.msg,'error');

      // , 
      if(editPost.index !== -1){
        this.updateThePost(editPost.index);
      }
      // this.refreshList();
    },

    // 
    updateThePost(index) {
      this.posts[index].id = this.posts[index].post.id;
      this.posts[index].title = this.posts[index].post.title.substr(0, 20) + "...";
      this.posts[index].content = this.posts[index].post.content.substr(0, 20) + "...";
      this.posts[index].id = this.posts[index].post.author;
    },

    // 
    cancelEdit(index) {
      console.log(' :',index);
      // , 
      PostDataService.get(this.posts[index].id)
          .then(response => {
            this.posts[index].post = response.data;
            console.log('');
          })
          .catch(e => {
            console.log(e);
          });
    },

    //mongodb()
    retrievePosts() {
      const params = this.getRequestParams(
          this.search,
          this.page,
          this.pageSize
      );
      //
      this.loading = true;
      PostDataService.getAll(params)
          .then(response => {
            const { posts, totalPages } = response.data;
            this.totalPages = totalPages;
            this.posts = posts.map(this.getDisplayPost);
            //(id)
            for (let [index, post] of new Map(this.posts.map((item, i) => [i, item]))){
              post['index'] = index;
              // console.log(index,post);
            }
            //
            this.loading = false;
            this.$send(', :'+response.data.totalItems, 'success', 1000);
            console.log(', :'+response.data.totalItems);
          })
          .catch(e => {
            console.log(e);
          });
    },

    //
    refreshList() {
      this.retrievePosts();
      this.currentPost = null;
      this.currentIndex = -1;

      console.log('');
    },

    //,
    removeAllPosts() {
      if(!this.isAdmin){
        this.retrievePosts();
        console.log(',!');
        this.$swal.fire({
          title: ',!',
          icon: 'error',
          html: '...<br>( : hjw : 123456)',
          confirmButtonText: '',
        })
      }
      else {
        this.$swal.fire({
          title: '',
          icon: 'warning',
          showCancelButton: true,
          confirmButtonColor: '#d33',
          confirmButtonText: '',
          cancelButtonText: ''
        }).then((result) => {
          if (result.isConfirmed) {
            PostDataService.deleteAll()
                .then(response => {
                  console.log(response.data);
                  this.$send('!', 'success');
                  this.refreshList();
                })
                .catch(e => {
                  console.log(e);
                  this.$send(e.toString(), 'error');
                });
          }
        })
      }
    },

    //,
    deletePost(id) {
      if(!this.isAdmin){
        this.retrievePosts();
        console.log(',!');
        this.$swal.fire({
          title: ',!',
          icon: 'error',
          html: '...<br>( : hjw : 123456)',
          confirmButtonText: '',
        })
      }
      else {
        this.$swal.fire({
          title: '?',
          icon: 'warning',
          showCancelButton: true,
          confirmButtonColor: '#d33',
          confirmButtonText: '',
          cancelButtonText: ''
        }).then((result) => {
          if (result.isConfirmed) {
            PostDataService.delete(id)
                .then(response => {
                  console.log(response.data);
                  // this.$send(response.data.message,'success');
                  this.$send('!','success');
                  this.refreshList();
                })
                .catch(e => {
                  console.log(e);
                  this.$send(e.toString(),'error');
                });
          }
        })
      }
    },
  },
  computed: {
    // 
    currentUser: function () {
      return  this.$store.state.auth.user;
    },
    // 
    isAdmin: function () {
      for (let role of this.currentUser.roles) {
        if (role==="ROLE_ADMIN"||role==="ROLE_MODERATOR"){
          return true;
        }
      }
      return false;
    },
  },
  // 
  mounted() {
    this.retrievePosts();
  }
};
</script>

<style>
.list {
  text-align: left;
  max-width: 750px;
  margin: auto;
}
.v-chip {
  user-select: none;
}

.theme--light .v-pagination .v-pagination__item {
  outline : 0;
}

li {
  outline : 0;
}
</style>
